@import url(https://fonts.googleapis.com/css2?family=Pavanam&display=swap);
body{font-family:'Pavanam', sans-serif;display:flex;flex-direction:column;align-items:center;justify-content:center}.container{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;padding:40px;margin:10px}.container:hover{box-shadow:0 10px 20px 0 rgba(0,0,0,0.3)}h1{text-align:center}button{min-width:65px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 5px 0 5px;font-size:15px;background-color:#e7e7e7;color:#000;font-weight:bolder;border:none;cursor:pointer;justify-content:center}button:hover{background-color:#fceeaf;color:black}.button-active{background-color:#b6970c;color:#161616;outline:none}.button{background-color:#e9c62a;color:#000;outline:none}

